var hex_sha1=require("./sha1.js");
var Weixin = {};
var ticket;
var shareOpt;
function createNonceStr() {
    var chars = "abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789";
    var str = "";
    var length = 16;
    for (var i = 0; i < length; i++) {
      str +=chars.substr(Math.floor(Math.random()*(chars.length-1)), 1);
    }
    return str;
}
/**
 * @function JSSDK
 */
Weixin.init = function (ticket,appID,shareObj,debug=false) {
    shareOpt=shareObj;
    var time=Math.floor(new Date().getTime()/1000);
    var nonceStr = createNonceStr();
    var url=window.location.href.split('#')[0];
    var string = "jsapi_ticket="+ticket+"&noncestr="+nonceStr+"&timestamp="+time+"&url="+url;
    var signature = hex_sha1(string);
    wx.config({
        debug: debug, // ,apialertpclogpc
        appId: appID, // 
        timestamp: time, // 
        nonceStr: nonceStr, // 
        signature: signature,// 1
        jsApiList: [
            'checkJsApi',
            'addCard',
            'onMenuShareTimeline',
            'onMenuShareAppMessage',
            'hideOptionMenu',
            'showOptionMenu',
            'startRecord',
            'stopRecord',
            'scanQRCode',
            'openAddress',
            'chooseWXPay',
            'previewImage'
        ], // JSJS2
        openTagList: ['wx-open-launch-weapp','wx-open-launch-app'],
    });
    Weixin.share(shareOpt);
}

/**
 *  
 */
Weixin.initPay = function(res, callback, failCallback) {
    wx.ready(function() {
        wx.chooseWXPay({
            timestamp: res.payRes.timeStamp, // jssdktimestamptimeStampS
            nonceStr: res.payRes.nonceStr, //  32 
            package: res.payRes.package, // prepay_idprepay_id=***
            signType: res.payRes.signType, // 'SHA1''MD5'
            paySign: res.payRes.paySign, // 
            success: function(data) {
                // 
                if(typeof callback == 'function') {
                    callback(data, res.orderID);
                }
            },
            cancel: function() {
                if(typeof failCallback == 'function') {
                    failCallback(res.orderID);
                }
            },
            fail: function() {
                if(typeof failCallback == 'function') {
                    failCallback(data, res.orderID);
                }
            }
        });
    });
};
/**
 * 
 */
Weixin.readAddress = function(callback) {
    wx.ready(function() {
        wx.openAddress({
            success: function(data) {
                if(typeof callback == 'function') {
                    callback(data);
                }
            },
            cancel: function() {}
        });
    });
};

/**
 * @function APIJS
 * @param 
 */
Weixin.call = function (method) {
    if ('WeixinJSBridge' in window) {
        WeixinJSBridge.call(method);
    } else {
        document.addEventListener('WeixinJSBridgeReady', function () {
            WeixinJSBridge.call(method);
        });
    }
};

/**
 * @function API
 */
Weixin.showOptionMenu = function () {
    // Weixin.call('showOptionMenu');
    wx.showOptionMenu();
};

/**
 * @function API
 */
Weixin.hideOptionMenu = function () {
    // Weixin.call('hideOptionMenu');
    wx.hideOptionMenu();
};

/**
 * @function 
 */
Weixin.share= function (shareOpt) {
    Weixin.showOptionMenu();
    wx.ready(function () {
        wx.onMenuShareTimeline({
            title: shareOpt.desc, // 
            link: shareOpt.link, // 
            imgUrl: shareOpt.imgUrl, // 
            success: function(data){
                if(typeof shareOpt['success']=="function"){
                    shareOpt['success']('');
                }
            }
        });

        wx.onMenuShareAppMessage({
            title: shareOpt.title||' ', // 
            desc: shareOpt.desc, //
            link: shareOpt.link, // 
            imgUrl: shareOpt.imgUrl, // 
            success: function(){
                if(typeof shareOpt['success']=="function"){
                    shareOpt['success']('');
                }
            }
        });
    })
};
/**
 * @function  
 * @param cardId 
 * @param cardExt
 */
Weixin.addCard = function (cardId, cardExt) {
    var deferred = $.Deferred();

    wx.addCard({
        cardList: [{
            cardId: cardId,
            cardExt: cardExt
        }],
        success: deferred.resolve.bind(deferred),
        cancel: deferred.reject.bind(deferred)
    });

    return deferred.promise();
};

/**
 * @function 
 */
Weixin.startRecord = function () {
    wx.startRecord();
};

/**
 * @function 
 */
Weixin.stopRecord = function (success) {
    wx.stopRecord({
        success: success
    });
};
Weixin.qr=function(){
    wx.scanQRCode({
        needResult: 0, // 01
        scanType: ["qrCode","barCode"], // 
        success: function (res) {
            var result = res.resultStr; // needResult  1 
        }
    });
}
Weixin.hideOptionMenu();
module.exports=Weixin;


