import query from "vendor/utils/getUrlParms";
import LS from "vendor/utils/cz-storage";
import Loading from "vendor/ui/loading";
let scene = query('scene')||localStorage.scene||'';
if( scene ) localStorage.scene = scene;
let appid = query('appid') || sessionStorage.appid || '';
let token = query('token') || sessionStorage.token || '';
let llbtoken = query('llbtoken') || sessionStorage.llbtoken || '';
if(appid) sessionStorage.appid = appid;
if(token) sessionStorage.token = token;
if(llbtoken) sessionStorage.llbtoken = llbtoken;

module.exports = {   
    //
    monopoly202112(data,opt={}){
        return request('monopoly2021/api/game61/info', data,Object.assign({type:'POST',isLoading:false }, opt));
    },
    //
    monopoly202112Lottery(data,opt={}){
        return request('monopoly2021/api/game61/lottery', data,Object.assign({type:'POST',isLoading:true }, opt));
    },
    //
    monopoly202112Winners(data,opt={}){
        return request('monopoly2021/api/game61/winners', data,Object.assign({type:'POST',isLoading:false }, opt));
    },
    //
    monopoly202112Prizeinfo(data,opt={}){
        return request('monopoly2021/api/game61/prize-info', data,Object.assign({type:'POST',isLoading:true }, opt));
    },
    //
    monopoly202112Prizelist(data,opt={}){
        return request('monopoly2021/api/game61/prize-list', data,Object.assign({type:'POST',isLoading:true }, opt));
    },
    //
    monopoly202112Invite(data,opt={}){
        return request('monopoly2021/api/game61/invite', data,Object.assign({type:'POST',isLoading:false }, opt));
    },
    //
    monopoly202112Like(data,opt={}){
        return request('monopoly2021/api/game61/like', data,Object.assign({type:'POST',isLoading:true }, opt));
    },
    //
    monopoly202112Qr(data,opt={}){
        return request('monopoly2021/api/game61/qr2', data,Object.assign({type:'POST',isLoading:false }, opt));
    },
    //
    monopoly202112Upload(data,opt={}){
        return request('monopoly2021/api/game61/upload', data,Object.assign({type:'POST',isLoading:true }, opt));
    },
    //
    monopoly202112Friends(data,opt={}){
        return request('monopoly2021/api/game61/friends', data,Object.assign({type:'POST',isLoading:true }, opt));
    },
    // 
    monopoly202112GetCard(data,opt={}){
        return request('monopoly2021/api/game61/get-card', data,Object.assign({type:'POST',isLoading:true }, opt));
    },
    // ling
    monopoly202112ExchangePlayCount(data,opt={}){
        return request('monopoly2021/api/game61/exchange-play-count', data,Object.assign({type:'POST',isLoading:true }, opt));
    },
    // 
    monopoly202112Gameover(data,opt={}){
        return request('monopoly2021/api/game61/gameover', data,Object.assign({type:'POST',isLoading:true }, opt));
    },
    // 
    monopoly202112Red(data,opt={}){
        return request('monopoly2021/api/game61/red', data,Object.assign({type:'POST',isLoading:true }, opt));
    },
    // 
    monopoly202112Gamestart(data,opt={}){
        return request('monopoly2021/api/game61/game-start', data,Object.assign({type:'POST',isLoading:false }, opt));
    },
}
function request(url, data={}, opt={}) {
    var dtd = $.Deferred();
    if( opt.isLoading ){
        Loading.show();
    }
    data.scene = data.scene||scene||'';
    data.activityCode = vueApp.config.activityCode;
    // llb
    if( window.userId&&window.token ){
        data.originalToken = window.token||'';
        data.userIdStr = window.userId||'';
    }   
    let ajaxHeader = {
        headers: {
            "Accept": "application/json; charset=utf-8",
            "appid": appid,
            "llbtoken": llbtoken,
            "authorization": token?'Bearer ' +token:'',
        },
        url: url.indexOf('http')!=-1?url:vueApp.config.serverPath+url,
        type: opt.type||"POST",
        data: data,        
    }
    if( opt.isUpload ){
        ajaxHeader.contentType = false;
        ajaxHeader.processData = false;
    }
    console.log('data',data);
    $.ajax(ajaxHeader).done((res,status,xhr) => {
        dtd.resolve(res); 
    }).fail(res => {
        Loading.hide();  
        dtd.resolve({success:false,msg:''});
        console.log('res',res);
    }).always((res) => {
        if( opt.isLoading ){
            Loading.hide();  
        }
    });
    return dtd;
}


